<template>
	<div class="sidebar">
		<div class="sidebar__layout sidebar__banner">
			<banner></banner> <!-- // banner -->
		</div>

		<div class="sidebar__layout sidebar__recommendations">
			<h2>Recommend</h2>

			<recommendation
				v-for="i in data"
				:key="i.id"
				:value="i"
			></recommendation>  <!-- // recommendation -->

			<ads></ads>  <!-- // ads -->
		</div>
	</div>  <!-- // sidebar -->
</template>

<script>
import Banner from '@/components/common/sidebar/Banner'
import Recommendation from '@/components/common/sidebar/Recommendation'
import Ads from '@/components/common/sidebar/Ads'

export default {
  name: 'sidebar',

	components: { Banner, Recommendation, Ads },

	data() {
		return {
			data: [
				{ id: 1, showHoverImage: false },
				{ id: 2, showHoverImage: false },
				{ id: 3, showHoverImage: false },
				{ id: 4, showHoverImage: false }
			]
		}
	}
}
</script>



