			<h3 class="article__title">サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキスト</h3>
			<div class="article__desc">サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキス</div>
		</div>
	</article>  <!-- // article -->
</template>

<script>
import Sidebar from '@/components/common/sidebar/Sidebar'

export default {
  name: 'app-article',
  components: { Sidebar },
	props: {
		value: {}
	}
}
</script>



