<template>
	<div class="app-content">
		<main class="app-content__layout app-content__layout--articles">
			<div class="app-content__articles">
				<app-article
					v-for="(i, key) in data"
					:value="i"
					:key="key"
				></app-article>
			</div>

			<div class="app-content__more">
				<a href="https://www.google.com"></a>
				<span>More</span>
				<i class="ico ico-arrow"></i>
			</div>
		</main>

		<aside class="app-content__layout app-content__layout--sidebar">
			<sidebar></sidebar>
		</aside>
	</div>  <!-- // app-content -->
</template>

<script>
import AppArticle from '@/components/common/AppArticle'
import Sidebar from '@/components/common/sidebar/Sidebar'

export default {
  name: 'app-content',
	components: { AppArticle, Sidebar },

	data() {
		return {
			showHoverImage: false,

			data: [
				{ id: 1, showHoverImage: false },
				{ id: 2, showHoverImage: false },
				{ id: 3, showHoverImage: false },
				{ id: 4, showHoverImage: false },
				{ id: 5, showHoverImage: false },
				{ id: 6, showHoverImage: false },
				{ id: 7, showHoverImage: false },
				{ id: 8, showHoverImage: false },
				{ id: 9, showHoverImage: false },
				{ id: 10, showHoverImage: false }
			]
		}
	},

	method: {}
}
</script>



